window.dom = {
    // å¢
    // åˆ›å»ºä¸€ä¸ªèŠ‚ç‚¹
    create(string) {
        const container = document.createElement("template");
        //templateå¯ä»¥å…¼å®¹æ‰€æœ‰çš„æ ‡ç­¾
        container.innerHTML = string.trim();
        // trim()  è¡¨ç¤ºæŠŠå­—ç¬¦ä¸²ä¸¤è¾¹çš„ç©ºæ ¼å»æ‰ã€‚
        return container.content.firstChild;
    },
    // å°†node2æ’å…¥åˆ°nodeçš„åé¢
    after(node, node2) {
        node.parentNode.insertBefore(node2, node.nextSibling);
    },
    // å°†node2æ’å…¥åˆ°nodeçš„å‰é¢
    before(node, node2) {
        node.parentNode.insertBefore(node2, node)
    },
    // åˆ›å»ºä¸€ä¸ªå­èŠ‚ç‚¹
    append(parent, node) {
        parent.appendChild(node)
    },
    // ç»™nodeåˆ›å»ºä¸€ä¸ªparentèŠ‚ç‚¹
    wrap(node, parent) {
        dom.before(node, parent)
        dom.append(parent, node)
    },
    // åˆ 
    // åˆ é™¤èŠ‚ç‚¹
    remove(node) {
        node.parentNode.removeChild(node)
        return node
    },
    // åˆ é™¤åä»£
    empty(node) {
        const { childNodes } = node // ç­‰äº const childNodes = node.childNodes
        const array = []
        let x = node.firstChild
        while (x) {
            array.push(dom.remove(node.firstChild))
            x = node.firstChild
        }
        return array
    },
    // å†™å±æ€§
    attr(node, name, value) {// é‡è½½
        // å¦‚æœå‚æ•°ä¸ªæ•°ä¸º3ï¼Œå°±æ”¹å†™
        if (arguments.length === 3) {
            node.setAttribute(name, value)
        }
        // å¦‚æœå‚æ•°ä¸ªæ•°ä¸º2ï¼Œå°±è¯»
        else if (arguments.length === 2) {
            return node.getAttribute(name)
        }
    },
    // ä¿®æ”¹æ–‡æœ¬å†…å®¹ï¼Œä¼šå°†åŸæœ‰çš„æ ‡ç­¾åˆ é™¤
    text(node, string) {   // é€‚é…
        // è¿™ä¸ªåˆ¤æ–­ç”¨æ¥å…¼å®¹æ‰€æœ‰çš„æµè§ˆå™¨
        if (arguments.length === 2) {
            if ('innerText' in node) {
                node.innerHTML = string// ie
            } else {
                node.textContent = string // firefox chrome
            }
        } else if (arguments.length === 1) {
            if ('innerText' in node) {
                return node.innerHTML // ie
            } else {
                return node.textContent  // firefox chrome
            }
        }
    },
    //ä¿®æ”¹html
    html(node, string) {
        if (arguments.length === 2) {
            node.innerHTML = string
        }
        else if (arguments.length === 1) {
            return node.innerHTML
        }
    },
    // ä¿®æ”¹style
    style(node, name, value) {
        if (arguments.length === 3) {
            // dom.style(div,'color','red') è®¾ç½®
            node.style[name] = value
        } else if (arguments.length === 2) {
            if (typeof name === 'string') {
                // dom.style(div,'color') è·å–
                return node.style[name]
            } else if (name instanceof Object) {
                // dom.style(div,{color:'red'}) è®¾ç½®
                const object = name
                for (let key in object) {
                    node.style[key] = object[key]
                }
            }
        }
    },
    //æ·»åŠ class
    class: {
        add(node, className) {
            node.classList.add(className)
        },
        //åˆ é™¤class
        remove(node, className) {
            node.classList.remove(className)
        },
        //æŸ¥è¯¢æ˜¯å¦å­˜åœ¨
        has(node, className) {
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn) {
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn) {
        node.removeEventListener(eventName, fn)
    },
    //æŸ¥æ‰¾æ ‡ç­¾æˆ–è€…æ ‡ç­¾ä»¬
    find(selector, scope) {
        //scope èŒƒå›´
        //å¦‚æœåˆscope åˆ™åœ¨scopeé‡Œæ‰¾ï¼Œæ²¡æœ‰ åˆ™åœ¨documenté‡Œæ‰¾
        return (scope || document).querySelectorAll(selector)
    },
    // æŸ¥æ‰¾çˆ¶å…ƒç´ 
    parent(node) {
        return node.parentNode
    },
    //æŸ¥æ‰¾å­å…ƒç´ 
    children(node) {
        return node.children
    },
    //æŸ¥æ‰¾å…„å¼ŸèŠ‚ç‚¹
    siblings(node) {
        return Array.from(node.parentNode.children)
            .filter(n => n !== node)
    },
    //æ‰¾åˆ°å…ƒç´ çš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹
    next(node) {
        let x = node.nextSibling
        while (x && x.nodeType === 3) {
            x = x.nextSibling
        }
        return x
    },
    // æ‰¾åˆ°å…ƒç´ çš„ä¸Šä¸€ä¸ªèŠ‚ç‚¹
    previous(node) {
        let x = node.previousSibling
        while (x && x.nodeType === 3) {
            x = x.previousSibling
        }
        return x
    },
    // ç”¨äºéå†æ‰€æœ‰èŠ‚ç‚¹
    each(nodeList, fn) {
        for (let i = 0; i < nodeList.length; i++) {
            fn.call(null, nodeList[i])
        }
    },
    // ç”¨äºè·å–å…ƒç´ æ’è¡Œè€å‡ 
    index(node) {
        const list = dom.children(node.parentNode)
        let i
        for (i = 0; i < list.length; i++) {
            if (list[i] === node) {
                break
            }
        }
        return i
    }


};

